.declarant {
  overflow: hidden;
  background-image: url("../../img/agree-icon.svg");
  background-repeat: no-repeat;
  background-position: 16px 16px;
  background-image: url("../../img/first-blue.svg");
}
.declarant__label {
  color: #3B4256;
  font-family: "Arial Rounded MT Bold", sans-serif;
  line-height: 24px;
  font-size: 18px;
  font-weight: 400;
  padding-top: 14px;
  padding-left: 46px;
  padding-bottom: 14px;
  padding-right: 62px;
  border-radius: 4px;
  border: 2px solid #F5F5F7;
}
.declarant__label::before {
  content: "";
  width: 20px;
  height: 20px;
  position: absolute;
  top: 16px;
  left: 16px;
  border-radius: 50%;
  border: 2px solid #3B4256;
}
.declarant__list {
  display: flex;
  flex-direction: column;
}
.declarant__item {
  width: 448px;
  position: relative;
  margin-bottom: 16px;
  border-radius: 4px;
  display: flex;
}
.declarant__item {
  background-color: #F5F5F7;
}
.declarant__input:checked ~ .declarant__label::before {
  content: "";
  width: 20px;
  height: 20px;
  position: absolute;
  top: 16px;
  left: 16px;
  border-radius: 50%;
  border: 6px solid #0061d9;
}
.declarant__input:checked ~ .declarant__label {
  background-color: #ffffff;
}
.declarant__input:checked ~ .declarant__label {
  border: 2px solid #3684E2;
  border-radius: 4px;
}
.declarant__wrap {
  display: flex;
  flex-direction: column;
}
