.agreement {
  overflow: hidden;
  height: 80px;
  background-image: url("../../img/agree-icon.svg");
  background-repeat: no-repeat;
  background-position: 16px 16px;
  background-image: url("../../img/third-gray.svg");
}
.agreement__notice {
  color: #3B4256;
  font-family: "Arial Rounded MT Bold", sans-serif;
  line-height: 20px;
  font-size: 14px;
  font-weight: 300;
  margin-top: 3px;
}
.agreement__list {
  list-style: none;
  margin-bottom: 17px;
}
.agreement__item {
  padding-left: 11px;
  margin-bottom: 8px;
  position: relative;
}
.agreement__item::before {
  content: "";
  width: 2px;
  height: 2px;
  position: absolute;
  top: 11px;
  left: 1px;
  border-radius: 50%;
  border: 2px solid #3B4256;
}
.agreement--block {
  padding-top: 12px;
  padding-left: 45px;
  padding-bottom: 16px;
  background-color: #F5F5F7;
  margin-bottom: 16px;
  position: relative;
  border: 2px solid #F5F5F7;
  border-radius: 4px;
}
.agreement--block::before {
  content: "";
  width: 20px;
  height: 20px;
  position: absolute;
  top: 16px;
  left: 16px;
  border-radius: 1px;
  border: 2px solid #3B4256;
}
.agreement__input:checked ~ .agreement--block::before {
  content: "";
  border: none;
  width: 22px;
  height: 22px;
  position: absolute;
  top: 14px;
  left: 14px;
  background-image: url("../../img/checkbox-on.svg");
}
.agreement__input:checked ~ .agreement--block {
  background-color: #ffffff;
  border: 2px solid #3684E2;
  border-radius: 4px;
}
.agreement--flex {
  display: flex;
}
.agreement__text {
  margin: 0;
  width: 340px;
  margin-bottom: 8px;
}
.agreement__warn {
  background-color: #FFF5E5;
  border-color: #FFF5E5;
  padding-left: 50px;
}
.agreement__warn::before {
  content: "";
  width: 24px;
  height: 24px;
  left: 14px;
  top: 14px;
  border: none;
  background-image: url("../../img/bell.svg");
}
.agreement__email {
  margin-bottom: 8px;
}
.agreement__wrap {
  display: none;
  flex-direction: column;
}
